/***************** Header ******************/

.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}
.header .logo-container {
  height: 100%;
  width: 70px;
  padding: 25px;
}
.header .options {
  width: 30%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header .options .option {
  padding: 10px 15px;
  cursor: pointer;
}
.header .logo-kapriz {
  content: "";
  background-position: center;
  background-size: cover;
  width: 80px;
  height: 80px;
  padding-left: 10px;
  margin-left: 20px;
}
.header .main-title {
  font-size: 4rem;
  /*font-family: "Montserrat Alternates", sans-serif;*/
  font-family: "Lora", serif;
  font-weight: bolder;
  letter-spacing: 3vw;
  margin-left: 25%;
  text-shadow: -4px 4px 2 blue;
}
.header a {
  text-decoration: none;
  color: black;
}

/************************************/
.title-l {
  font-family: "Comfortaa";
  font-size: 2rem;
}
.title-2 {
  text-align: center;
  font-family: "Open Sans Condensed";
  letter-spacing: 2px;
  font-weight: bold;
  font-size: 2.5rem;
}
